@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  box-sizing: border-box;
  background-color: rgb(214, 237, 255);
}

nav {
  height: 30px;
  background-color: #5941A9;
  color: #E5EBEA;
  text-align: center;
}
div.App {
  max-height: 100%;
  max-width: 1034px;
  margin: 0 auto;
  border-left: 5px solid #5941A9;
  border-right: 5px solid #5941A9;
}
header {
    height: 8vh;
    background-color: black;
    color: #FBFBFF;
    text-align: center;
    font-size: 4vh;
    font-weight: bold;
    line-height: 8vh;
    text-transform: uppercase;
}
.smallitem {
    display: block;
    max-width: 100px;
    max-height: 50px;
    border: 1px solid black;
    margin: 5px;
}

.smallitem:hover {
    -webkit-filter: brightness(50%);
            filter: brightness(50%);
    transition: 0.2s;
    cursor: pointer;
}
.detail img {
    max-height: 125px;
}

.detail p {
    font-size: 20px;
}

.detail {
    padding-left: 50px;
}
button.red {
    color: #e74c3c;
}

button.red:hover {
    color: #fff;
    background-color: #e74c3c;
}

button.red.active {
    color: #fff;
    background-color: #e74c3c;
}

button.white:hover {
    color: #fff;
    background-color: black;
}

button.white.active {
    color: #fff;
    background-color: black;
}

button.black:hover {
    color: white;
    background-color: black;
}

button.black.active {
    color: white;
    background-color: black;
}

